import cTitle from 'components/title';	
  export default {
    data() {
      return {
 		value:null,
     list:[]    
       };
    },
    methods: {
      //发起http请求
		getNetData() {
			var that = this;
			$http.get('refund.list', { page: 1 }, '正在获取订单...').then(function (response) {

				if (response.result == 1) {
					
					var myData = response.data;
          that.list = myData.data;
					

				}
			}, function (response) {
				// error callback
			});

		},

    },
    activated () {
      this.getNetData();
      
    },
    components: { cTitle}
  };


